import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { Observable } from 'rxjs';

import { SERVER_API_URL } from 'app/app.constants';
import { createRequestOption } from 'app/shared/util/request-util';
import { IAuteur } from 'app/shared/model/auteur.model';

type EntityResponseType = HttpResponse<IAuteur>;
type EntityArrayResponseType = HttpResponse<IAuteur[]>;

@Injectable({ providedIn: 'root' })
export class AuteurService {
  public resourceUrl = SERVER_API_URL + 'api/auteurs';

  constructor(protected http: HttpClient) {}

  create(auteur: IAuteur): Observable<EntityResponseType> {
    return this.http.post<IAuteur>(this.resourceUrl, auteur, { observe: 'response' });
  }

  update(auteur: IAuteur): Observable<EntityResponseType> {
    return this.http.put<IAuteur>(this.resourceUrl, auteur, { observe: 'response' });
  }

  find(id: number): Observable<EntityResponseType> {
    return this.http.get<IAuteur>(`${this.resourceUrl}/${id}`, { observe: 'response' });
  }

  query(req?: any): Observable<EntityArrayResponseType> {
    const options = createRequestOption(req);
    return this.http.get<IAuteur[]>(this.resourceUrl, { params: options, observe: 'response' });
  }

  delete(id: number): Observable<HttpResponse<{}>> {
    return this.http.delete(`${this.resourceUrl}/${id}`, { observe: 'response' });
  }
}
